import Headers from "../components/header/header/Headers";
import Footer from "../components/header/footer/Footer";

function OqueFazemos(){
    return(
        <>
        <Headers/>
           <div className="oq-container">
           
          
        <div class="px-4 py-1 text-center texto-sobre">
          <div class="col-lg-6 mx-auto">
            <p class="lead mb-4 pt-3">Com 30 anos de mercado, a HIPERQUIMICA se consagrou como um estdio de referncia em decupagem cinematogrfica, produzindo storyboards e animatics para grandes marcas, sempre em parceria com as maiores agncias e produtoras de So Paulo. Nossa equipe de talentos entende tudo de cinema  de lentes e movimentos de cmera a direo de arte e fotografia. Com as novas ferramentas de IA, nossa criatividade ganhou um impulso extra, permitindo criar materiais realistas que obtiveram timos resultados e superaram as expectativas de nossos clientes.</p>
            <p class="lead mb-4 ">A HIPERQUIMICA est comeando uma nova fase na gerao de imagens com IA. Aps um ano de estudos, testes e trabalho intenso, chegamos a um formato ideal que permite criar imagens realistas de grande impacto, mockups, cenas do cotidiano, imagens reais e muito mais. Um toque da magia cinematogrfica em cada projeto.</p>
           
          </div>
        </div>
      
      
              


           </div>
        <Footer/>
        </>
    )


}

export default OqueFazemos;